<template>
        <div class="full-screen-map">
            <div class="col-md-12">
                <div class="card">
                    <center>
                        <div class="card-header">
                            <back-button></back-button>
                            <router-link
                                class="btn btn-info"
                                :to="{
                                    name: 'tickets.scan',
                                    params: {
                                        id: this.$route.params.id,
                                    },
                                }"
                            >
                                PICKUP
                            </router-link>
                            <button type="button" class="btn btn-danger">
                                DROPOFF
                            </button>
                        </div>
                        <div
                            class="card-body"
                            v-for="datas in passenger"
                            :key="datas.id"
                        >
                            <div v-if="datas.pickup == true">
                                <b>{{ datas.name }}</b> -
                                <i class="material-icons" style="color: green"
                                    >check</i
                                >
                                <h5>
                                    <b>PICKUP TIME:{{ datas.start_time }}</b>
                                    <b>DROPOFF TIME:{{ datas.start_time }}</b>
                                </h5>
                            </div>
                            <div v-else>
                                <b>{{ datas.name }}</b> -
                                <i class="material-icons" style="color: red"
                                    >block</i
                                >
                            </div>
                        </div>
                    </center>
                </div>
            </div>
        </div>
</template>
<script>
import * as fb from "../../firebase";

export default {
    data() {
        return {
            passenger: [],
        };
    },
    mounted() {
        var id = this.$route.params.id;
        fb.ticketsCollection.doc(id).onSnapshot((response) => {
            var passengerList = response.data().passengerID;
            this.passenger = passengerList;
        });
    },
};
</script>
